import React from 'react'
import ReactDOM from 'react-dom'
import {Button} from '../../lib/index'

ReactDOM.render(<Button></Button>,document.querySelector('.s-button-normal'));
ReactDOM.render(<Button type="primary"></Button>,document.querySelector('.s-button-primary'));
ReactDOM.render(<Button type="ghost"></Button>,document.querySelector('.s-button-ghost'));
ReactDOM.render(<Button type="link"></Button>,document.querySelector('.s-button-link'));


